.block-posts-carousel {
  max-width: 100%;
}
.block-posts-carousel__inner {
  padding: 35px 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .block-posts-carousel__inner {
    padding: 35px 0;
  }
}
.block-posts-carousel__top {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .block-posts-carousel__top {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }
}
.block-posts-carousel__top .posts-carousel {
  max-width: 100%;
  margin: 35px 0;
}
@media (min-width: 1024px) {
  .block-posts-carousel__top .posts-carousel {
    width: 50%;
    padding: 0 15px;
    flex: 0 0 50%;
  }
}
@media (min-width: 1441px) {
  .block-posts-carousel__top .posts-carousel {
    width: 66%;
    padding: 0 15px;
    flex: 0 0 66%;
  }
}
.block-posts-carousel__top .posts-carousel__carousel {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.block-posts-carousel__top .posts-carousel__carousel .block-title {
  margin-bottom: 20px;
  flex: 0 1 400px;
  color: rgb(var(--primary-color));
}
.block-posts-carousel__top .posts-carousel__carousel .block-title span,
.block-posts-carousel__top .posts-carousel__carousel .block-title strong {
  color: rgb(var(--secondary-color));
}
@media (min-width: 768px) {
  .block-posts-carousel__top .posts-carousel__carousel .block-title {
    margin-bottom: 60px;
  }
}
@media (min-width: 1024px) {
  .block-posts-carousel__top .posts-carousel__carousel .block-title {
    margin-bottom: 20px;
  }
}
@media (min-width: 1441px) {
  .block-posts-carousel__top .posts-carousel__carousel .block-title {
    margin-bottom: 60px;
    flex-basis: 500px;
  }
}
.block-posts-carousel__top .posts-carousel__carousel .swiper-pagination {
  width: 100%;
  margin: 25px 0;
  padding: 10px 15px;
  position: relative;
  flex: 1;
}
@media (min-width: 1024px) {
  .block-posts-carousel__top .posts-carousel__carousel .swiper-pagination {
    flex: 0 0 100%;
    text-align: right;
  }
}
@media (min-width: 1441px) {
  .block-posts-carousel__top .posts-carousel__carousel .swiper-pagination {
    margin-bottom: 60px;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    flex: 1;
  }
}
.block-posts-carousel__top .posts-carousel__carousel .swiper-pagination .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  margin: 0 5px;
  background-color: rgb(var(--primary-color));
  opacity: 1;
}
.block-posts-carousel__top .posts-carousel__carousel .swiper-pagination .swiper-pagination-bullet-active {
  background-color: rgb(var(--secondary-color));
}
.block-posts-carousel__top .posts-carousel__wrapper {
  flex: 0 0 100%;
}
.block-posts-carousel__top .posts-carousel__wrapper .post__inner {
  padding: 0 15px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.block-posts-carousel__top .posts-carousel__wrapper .post__pic {
  width: 100%;
  height: 205px;
  display: block;
  object-fit: cover;
  object-position: center center;
}
@media (min-width: 768px) {
  .block-posts-carousel__top .posts-carousel__wrapper .post__pic {
    height: 540px;
  }
}
@media (min-width: 1024px) {
  .block-posts-carousel__top .posts-carousel__wrapper .post__pic {
    height: 305px;
  }
}
@media (min-width: 1441px) {
  .block-posts-carousel__top .posts-carousel__wrapper .post__pic {
    height: 250px;
  }
}
.block-posts-carousel__top .posts-carousel__wrapper .post__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
}
.block-posts-carousel__top .posts-carousel__wrapper .post__title {
  color: rgb(var(--primary-color));
}
.block-posts-carousel__top .posts-carousel__wrapper .post__excerpt {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .block-posts-carousel__top .posts-carousel__wrapper .post__excerpt {
    font-size: 1.6rem;
    line-height: 3rem;
  }
}
.block-posts-carousel__top .posts-carousel__wrapper .post__permalink {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 2rem;
  color: rgb(var(--secondary-color));
  transition: 0.3s linear;
}
@media (min-width: 768px) {
  .block-posts-carousel__top .posts-carousel__wrapper .post__permalink {
    font-size: 1.6rem;
    line-height: 3rem;
  }
}
.block-posts-carousel__top .posts-carousel__wrapper .post__permalink span {
  font-size: 1.2rem;
  line-height: 2.2rem;
  font-weight: 600;
}
.block-posts-carousel__top .posts-carousel__wrapper .post__permalink:hover {
  color: rgb(var(--primary-color));
}
.block-posts-carousel__top .posts-form {
  width: 100%;
  margin: 35px 0;
  padding: 40px 30px;
  background: #fff;
  box-shadow: 0 0 24px rgba(0, 0, 0, 0.1);
}
@media (min-width: 1024px) {
  .block-posts-carousel__top .posts-form {
    flex: 0 0 50%;
  }
}
@media (min-width: 1441px) {
  .block-posts-carousel__top .posts-form {
    flex: 0 0 33%;
  }
}
.block-posts-carousel__top .posts-form .form {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.block-posts-carousel__top .posts-form .form__title {
  color: rgb(var(--primary-color));
}
.block-posts-carousel__top .posts-form .form__title span,
.block-posts-carousel__top .posts-form .form__title strong {
  color: rgb(var(--secondary-color));
}
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container select,
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container option,
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container input,
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container textarea {
  max-width: 100%;
  padding: 15px;
  font-family: var(--roboto);
  font-size: 2rem;
  color: #bcbcbc;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0;
}
@media (min-width: 768px) {
  .block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container select,
  .block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container option,
  .block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container input,
  .block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container textarea {
    font-size: 1.6rem;
    line-height: 3rem;
  }
}
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container select::placeholder,
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container option::placeholder,
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container input::placeholder,
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container textarea::placeholder {
  color: #bcbcbc;
}
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container .wpforms-field {
  max-width: 100%;
  width: 100%;
  padding: 0 0;
}
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container .wpforms-field fieldset {
  flex: 0 0 100%;
  display: flex;
}
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container .wpforms-field fieldset .wpforms-field-row {
  max-width: 100%;
  flex: 0 0 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container .wpforms-field fieldset .wpforms-field-row::after, .block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container .wpforms-field fieldset .wpforms-field-row::before {
  content: none;
}
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-field-container .wpforms-field fieldset .wpforms-field-row .wpforms-field-row-block {
  width: 100%;
  margin-bottom: 0;
  padding: 0;
}
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-submit-container {
  margin-top: 20px;
}
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-submit-container [type=submit] {
  appearance: none;
  padding: 16px 28px;
  background-color: rgb(var(--secondary-color));
  border: none;
  text-transform: capitalize;
  font-weight: normal;
  font-size: 2rem;
  color: #fff;
  cursor: pointer;
}
@media (min-width: 768px) {
  .block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-submit-container [type=submit] {
    font-size: 1.6rem;
    line-height: 3rem;
  }
}
.block-posts-carousel__top .posts-form .form__wrapper .wpforms-form .wpforms-submit-container [type=submit]:hover {
  background-color: rgb(var(--primary-color));
}
.block-posts-carousel__bottom {
  width: 100%;
  padding: 70px 0;
}
.block-posts-carousel__bottom .settlements {
  padding: 0 15px;
}
.block-posts-carousel__bottom .settlements__inner {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
@media (min-width: 768px) {
  .block-posts-carousel__bottom .settlements__inner {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 30px;
    flex-wrap: wrap;
  }
}
.block-posts-carousel__bottom .settlements__inner .settlement {
  margin: 0;
  padding: 45px 30px 35px;
  position: relative;
  box-shadow: 0 0 24px rgba(0, 0, 0, 0.1);
}
@media (min-width: 768px) {
  .block-posts-carousel__bottom .settlements__inner .settlement {
    flex: 0 0 calc(50% - 15px);
  }
}
@media (min-width: 1441px) {
  .block-posts-carousel__bottom .settlements__inner .settlement {
    flex: 0 0 calc(25% - 30px);
  }
}
.block-posts-carousel__bottom .settlements__inner .settlement .icon-wrapper {
  width: 120px;
  height: 135px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
  display: flex;
}
.block-posts-carousel__bottom .settlements__inner .settlement .icon-wrapper img,
.block-posts-carousel__bottom .settlements__inner .settlement .icon-wrapper svg {
  width: 100%;
  height: 100%;
  display: block;
  opacity: 0.06;
}
.block-posts-carousel__bottom .settlements__inner .settlement .content .amount {
  margin-bottom: 15px;
  padding-bottom: 0;
  display: block;
  font-size: 50px;
  font-weight: 600;
  line-height: 1;
  color: rgb(var(--secondary-color));
}
.block-posts-carousel__bottom .settlements__inner .settlement .content .concept {
  margin-bottom: 5px;
  font-family: var(--roboto);
  font-size: 2rem;
  font-weight: 500;
  letter-spacing: 1px;
  color: rgb(var(--primary-color));
}
@media (min-width: 768px) {
  .block-posts-carousel__bottom .settlements__inner .settlement .content .concept {
    font-size: 1.6rem;
    line-height: 3rem;
  }
}